import axios from '@/utils/uniAxios.js'
import share from '@/components/share';
export default{
	components: {
		share
	},
	data() {
		return {
			specClass: 'none',
			specSelected:[],
			productInfo: {
				albumPics: [
					'https://gd3.alicdn.com/imgextra/i3/0/O1CN01IiyFQI1UGShoFKt1O_!!0-item_pic.jpg_400x400.jpg',
					'https://gd3.alicdn.com/imgextra/i3/TB1RPFPPFXXXXcNXpXXXXXXXXXX_!!0-item_pic.jpg_400x400.jpg',
					'https://gd2.alicdn.com/imgextra/i2/38832490/O1CN01IYq7gu1UGShvbEFnd_!!38832490.jpg_400x400.jpg'
				],
			},
			favorite: true,
			shareList: [],
			
			desc: `
				<div style="width:100%">
					<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i4/479184430/O1CN01nCpuLc1iaz4bcSN17_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd2.alicdn.com/imgextra/i2/479184430/O1CN01gwbN931iaz4TzqzmG_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i3/479184430/O1CN018wVjQh1iaz4aupv1A_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd4.alicdn.com/imgextra/i4/479184430/O1CN01tWg4Us1iaz4auqelt_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd1.alicdn.com/imgextra/i1/479184430/O1CN01Tnm1rU1iaz4aVKcwP_!!479184430.jpg_400x400.jpg" />
				</div>
			`,
			specList: [
				{
					id: 1,
					name: '尺寸',
				},
				{	
					id: 2,
					name: '颜色',
				},
			],
			specChildList: [
				{
					id: 1,
					pid: 1,
					name: 'XS',
				},
				{
					id: 2,
					pid: 1,
					name: 'S',
				},
				{
					id: 3,
					pid: 1,
					name: 'M',
				},
				{
					id: 4,
					pid: 1,
					name: 'L',
				},
				{
					id: 5,
					pid: 1,
					name: 'XL',
				},
				{
					id: 6,
					pid: 1,
					name: 'XXL',
				},
				{
					id: 7,
					pid: 2,
					name: '白色',
				},
				{
					id: 8,
					pid: 2,
					name: '珊瑚粉',
				},
				{
					id: 9,
					pid: 2,
					name: '草木绿',
				},
			]
		};
	},
	async onLoad(options){
		
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = options.id;
		if(id){
			this.productInfoById(id);
			this.$api.msg(`点击了${id}`);
		}
		
		
		//规格 默认选中第一条
		this.specList.forEach(item=>{
			for(let cItem of this.specChildList){
				if(cItem.pid === item.id){
					this.$set(cItem, 'selected', true);
					this.specSelected.push(cItem);
					break; //forEach不能使用break
				}
			}
		})
		this.shareList = await this.$api.json('shareList');
	},
	methods:{
		//规格弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(index, pid){
			let list = this.specChildList;
			list.forEach(item=>{
				if(item.pid === pid){
					this.$set(item, 'selected', false);
				}
			})

			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = []; 
			list.forEach(item=>{ 
				if(item.selected === true){ 
					this.specSelected.push(item); 
				} 
			})
			
		},
		//分享
		share(){
			this.$refs.share.toggleMask();	
		},
		//收藏
		toFavorite(){
			this.favorite = !this.favorite;
		},
		buy(){
			uni.navigateTo({
				url: `/pages/order/createOrder`
			})
		},
		addCart() {
			const {id, productCategoryId} = this.productInfo;
			axios.post('/cart/add', {productId: id, productCategoryId}).then(({data}) => {
				if (data.code === 200) {
					this.$api.msg('已成功加入购物车')
				}
			});
		},
		productInfoById(id) {
			axios.post('/product/productInfoById', {id}).then(({data}) => {
				console.log(data);
				if (data.code === 200) {
					this.productInfo = data.data;
					this.productInfo.albumPics = this.productInfo.albumPics.split(',');
				}
			})
		},
		stopPrevent(){}
	},

}
